.media{
	cursor: pointer;
	margin-right: 15px;
	display: inline-block;
	width: 260px;
	vertical-align: top;
}
.media:hover .media-cover{
	-webkit-box-shadow: 0 0 10px rgba(0,0,0,.3);
	        box-shadow: 0 0 10px rgba(0,0,0,.3);
}
.media-cover{
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	-webkit-box-shadow: 0 0 5px rgba(0,0,0,.3);
	        box-shadow: 0 0 5px rgba(0,0,0,.3);
	height: 160px;
}

.media-image{
	  -o-object-fit: cover;
	     object-fit: cover;
}


.media-title{
margin: 5px 0 0 0;
  color: #44546b;
  white-space: normal;
}

.media-author {
  font-size: 12px;
  margin: 5px 0 0 0;
}
@media (max-width: 1110px) {
  .media {
    width: 45%;
     padding: 6px;
    border-radius: 5px;
    border: 2px solid #cccccc;
     margin-bottom: 30px;
      text-align: center;
   -ms-flex-line-pack: center;
       align-content: center;
	}


}
@media (max-width: 900px) {
  .media {
    width: 95%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 30px;
    padding: 6px;
    border-radius: 5px;
    border: 2px solid #cccccc;
  }

  .media-author{
  	display: none;
  }
  .media-title{
      padding: 10px;
  }
}
@media (max-width: 470px) {
  .media {
    -ms-flex-direction: column;
        flex-direction: column;
   text-align: center;
   -ms-flex-line-pack: center;
       align-content: center;
    border: 0 solid #b5f2f2; 
  }

	.media-image{
		  -ms-flex-item-align: center;
		      align-self: center;
		  border-radius: 5px;
	}
}



