.Search {
  max-width: 400px;
  margin-bottom: 20px;
}

.Search-input {
  background: #f2f2f2;
  padding: 20px;
  font-size: 18px;
  font-family: Arial;
  width: 100%;
  border-radius: 10px;
  border:none;
  outline: 0;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  -webkit-box-shadow: 2px 2px 5px rgba(0,0,0,.2);
          box-shadow: 2px 2px 5px rgba(0,0,0,.2);
}

.Search-input:focus {
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,.5);
          box-shadow: 0 0 10px rgba(0,0,0,.5);
}
@media (max-width: 600px) {
  .Search-input {

  width: 80%;
 
  }
}


